html, {
  box-sizing: border-box;
}

body {
  min-width: $mobile-width;

  font-family: $main-font;
  font-size: 17px;
  line-height: 24px;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
